<template>
    <section>
        <t-title :title="obj.name " v-if="obj.list  && obj.list.length"></t-title>
        <ul class="goods">
            <v-integral v-if="obj.name !=='积分兑换券'"  :item="i" jumpTo="" v-for="(i,index) in obj.list" :key="index"></v-integral>
            <coupon v-if="obj.name =='积分兑换券'" :item="i" jumpTo="" v-for="(i,index) in obj.list" :key="index"></coupon>
        </ul>
    </section>
</template>

<script>
    import vIntegral from '../../common/list/Integral.vue'
    import coupon from '../../common/list/coupon.vue'
    import tTitle from '../../index/home/top-title.vue'
    export default {
        props: {
            obj: {
                type: Object,
                required: true,
                default() {
                    return {
                        name:'',
                        list:[]
                    }
                }
            },
            title:{
                type: String
            }
        },
        mounted () {
            // console.log('obj')
            // console.log(this.obj)
        },
        components: {
            vIntegral,
            tTitle,
            coupon
        }
    }
</script>
<style lang="less" scoped>
    @import '../../../assets/less/common.less';
    ul{
        margin: 0;
        padding: 0;
        background: #fff;
        display: flex;
        flex-wrap: wrap;
    }
</style>



